.FinishedQuiz {
    color: white;
    padding: 20px;
    border: 2px solid #ffffff;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
}

.FinishedQuiz ul {
    list-style: none;
    padding: 0;
    margin: 0;

}

.FinishedQuiz ul li i {
    margin-left: 10px;

}

.success, .yes {
    color: green;

}

.error, .no {
   color: red;

}