.Button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #000;
    margin-right: 15px;
    text-transform: uppercase;
    text-weight: bold;
    text-size: 12px;
    cursor: pointer;
}

.Button:focus {
    outline: none;

}

.Button:active {
    box-shadow: inset 2px 2px rgba(0, 0, 0, .3);
}

.Button:disabled {
    background: #ccc;
    color:#000;
    cursor: not-allowed;
}

.error {
    background: red;
    color: #fff;
}
.success{
    background: green;
    color: #fff;
}

.primary{
    background: #2884f6;
    color: #fff;

}